import Vue from 'vue';
import VueRouter from 'vue-router';
import Mark from 'pages/Mark';
import TaskMark from 'pages/Mark/TaskMark';
import FileAdmin from 'pages/FileAdmin';
import TaskAdmin from 'pages/FileAdmin/TaskAdmin';

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: '/mark',
      component: Mark,
      children: [
        {
          path: 'taskMark',
          component: TaskMark,
        },
      ],
    },
    {
      path: '/fileAdmin',
      component: FileAdmin,
      children: [
        {
          path: 'TaskAdmin',
          component: TaskAdmin,
        },
      ],
    },
  ],
});
